import React, {useState} from 'react';
import {  signInWithEmailAndPassword   } from 'firebase/auth';
import { auth } from '../Firebase/firebase';
import { NavLink, useNavigate } from 'react-router-dom';
import { styles }from '../Authentication/Login.css';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Login = () => {
    const navigate = useNavigate();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const onLogin = async (e) => {
        e.preventDefault();
        try {
            const userCredential = await signInWithEmailAndPassword(auth, email, password);
            const user = userCredential.user;

            // Store the access token (user's UID) in session storage
            sessionStorage.setItem('accessToken', user.uid);
            console.log(user);

            toast.success('Login successful!', {
                position: 'top-right',
                autoClose: 4000, // Close after 3 seconds
                hideProgressBar: true,
              });

            // Navigate to the home page or any other route
            navigate('/home');
          } catch (error) {
            const errorCode = error.code;
            const errorMessage = error.message;
            console.log(errorCode, errorMessage);

              // Show error toast
            toast.error('Login failed. Please check your credentials.', {
                position: 'top-right',
                autoClose: 4000,
                hideProgressBar: true,
            });
          }

        };


    return(
        <>
            <main >

                    <div className="box">
                        <div className="header">
                        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                            <img  className="moving-car" src="data:image/png;base64," alt="" />
                        </div>
                            <h1 className="header-text">Log In</h1>
                            <p style={{textAlign: 'center'}}>Welcome!!</p>
                        </div>

                        <form className="login-Form" action="/login" method="post">

                        <div className="email-input">
                                <input
                                    id="email-address"
                                    name="email"
                                    type="email"
                                    required
                                    placeholder="Email address"
                                    onChange={(e)=>setEmail(e.target.value)}
                                    />
                        </div>

                        <div className="password-input">
                        <input
                            id="password"
                            name="password"
                            type="password"
                            required
                            placeholder="Password"
                            onChange={(e)=>setPassword(e.target.value)}
                            />
                        </div>

                        <div className="login-btn">
                            <button type="submit" name="button" onClick={onLogin}>
                                Log in
                            </button>
                        </div>

                        </form>

                        <div className="choose">
                        <p>
                            Need an account? { ` ` }
                            <NavLink to="/signup">
                                Sign up
                            </NavLink>
                        </p>
                        <p>
                            Or go <a href="/">Home</a>.
                        </p>
                        </div>


                    </div>
                </main>
            </>
            )
        }

export default Login
